import React, {useState, useEffect} from 'react';

const Return = React.memo(
    (props) =>{
        let [event, setEvent] = useState('');
        useEffect( ()=>{
            if(props.location.pathname.split('/')[2]!==undefined)
                setEvent(props.location.pathname.split('/')[2]);
        },[]);
        document.title = 'Спасибо за оплату'
        return (
            <main className='main main-about'>
                <div className='container'>
                    <div className='row'>
                        <div className='col-12'>
                            <div className='recovery-wrap'>
                                <div className='recovery-title'>
                                    <h1>Спасибо за оплату</h1>
                                </div>
                                <p>Ваша транзакция прошла успешно.</p>
                                {event.length>0?
                                    <div>
                                        <p>Вы можете скачать чек по ссылке ниже. Так же он был отправлен вам на почту указанную при регестрации.</p>
                                        <p><a href={'https://kassir.kg/visa/'+event} download>Скачать чек</a></p>
                                    </div>
                                    :
                                    null
                                }
                            </div>
                        </div>
                    </div>
                </div>
            </main>

        );
    })

export default Return;
